* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.button-box {
  justify-content: center;
  align-items: center;
  gap: 30px;
  display: flex;
}

.color-switch {
  color: #fafafa;
  background-color: red;
  border-radius: 10px;
  padding: 20px 40px;
  font-family: monospace;
  font-size: 20px;
  font-weight: 700;
  box-shadow: 5px 5px 12px #000;
}

.color-switch:first-of-type {
  background-color: #006400;
}

.timer {
  justify-content: center;
  gap: 40px;
  display: flex;
  position: relative;
  top: 30rem;
}

.field {
  color: gray;
  flex-direction: column;
  align-items: center;
  font-size: 15px;
  display: flex;
}

.value {
  color: #000;
  font-family: monospace;
  font-size: 60px;
  font-weight: 600;
}

